<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 50px);width: 155px;background-color: #ecedf1">
      <div style="font-size: 18px;font-weight: bold;text-align: center;margin: 10px 0 10px 0;color:grey">设备信息</div>
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/deviceList" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">设备列表</span>
        </el-menu-item>

        <div style="font-size: 18px;font-weight: bold;text-align: center;margin: 10px 0 10px 0;color:grey">点检标准录入</div>

        <el-menu-item index="/entryForm" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">标准录入</span>
        </el-menu-item>
        <el-menu-item index="/standardView" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">标准查看</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 160px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <div style="padding: 20px 0 0 20px;width: 100%">
      <!--  勾选设备＋状态选择页面-->
      <!--页面二合一-->
      <div>
        <el-input style="width: 150px;margin-right: 5px" placeholder="查询设备编号" prefix-icon="el-icon-search" v-model="equipmentNum"></el-input>
        <el-input style="width: 150px;margin-right: 5px" placeholder="查询设备名称" prefix-icon="el-icon-search" v-model="equipmentName"></el-input>
        <el-input style="width: 150px" placeholder="查询设备部门" prefix-icon="el-icon-search" v-model="department"></el-input>
        <el-button type="primary" style="margin-left: 10px" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
      </div>
      <div style="margin: 10px 0">
        <el-button type="primary" plain @click="handleAdd">新增</el-button>
        <el-button type="danger" plain @click="delBatch">批量删除</el-button>
      </div>
      <el-table :data="tableData" stripe :header-cell-style="{ backgroundColor: 'aliceblue', color: '#666' }" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="id" label="序号" width="70" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="equipmentNum" label="设备编号" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="equipmentName" label="设备名称" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="department" label="设备部门" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="操作" align="center" width="180" fixed="right" :show-overflow-tooltip="true">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="handleEdit(scope.row)">编辑</el-button>
            <el-button size="mini" type="danger" plain @click="del(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

      <el-dialog title="设备信息" :visible.sync="fromVisible" width="40%" :close-on-click-modal="false">
        <el-form :model="form" label-width="80px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="设备编号" prop="equipmentNum">
            <el-input v-model="form.equipmentNum" placeholder="设备编号"></el-input>
          </el-form-item>
          <el-form-item label="设备名称" prop="equipmentName">
            <el-input v-model="form.equipmentName" placeholder="设备名称"></el-input>
          </el-form-item>
          <el-form-item label="所属部门" prop="department">
            <el-input v-model="form.department" placeholder="设备所属部门"></el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>

    </div>
  </div>


</template>

<script>
export default {
  name: "ChooseDevice",
  data() {
    return {
      tableData: [],  // 所有的数据
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      equipmentName: '',
      equipmentNum: '',
      department: '',
      total: 0,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      rules: {
        equipmentNum: [
          { required: true, message: '请输入设备编号', trigger: 'blur' },
        ],
        equipmentName: [
          { required: true, message: '请输入设备名称', trigger: 'blur' },
        ],
        department: [
          { required: true, message: '请输入设备所属部门', trigger: 'blur' },
        ]
      },
      ids: [],
    }
  },
  created() {
    this.load()
  },
  methods: {
    changeOpen(form) {
      // 调用更新的接口  更新数据到数据库
      this.form = JSON.parse(JSON.stringify(form))
      this.sendSaveRequest()   // 直接发请求就可以了
    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/entryForm/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/entryForm/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible = true   // 打开弹窗
    },
    handleAdd() {   // 新增数据
      this.form = {}  // 新增数据的时候清空数据
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.sendSaveRequest()
        }
      })
    },
    sendSaveRequest() {
      this.$request({
        url: this.form.id ? '/entryForm/update': '/entryForm/add',
        method: this.form.id ? 'PUT' : 'POST',
        data: this.form
      }).then(res => {
        if (res.code === '200') {  // 表示成功保存
          this.$message.success('保存成功')
          this.load(1)
          this.fromVisible = false
        } else {
          this.$message.error(res.msg)  // 弹出错误的信息
        }
      })
    },
    reset() {
      this.equipmentNum = ''
      this.equipmentName = ''
      this.department = ''
      this.load()
    },
    load(pageNum) {  // 分页查询
      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/entryForm/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          equipmentNum: this.equipmentNum,
          equipmentName: this.equipmentName,
          department: this.department,
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
  }
}
</script>

<style>
.el-tooltip__popper{ max-width:300px !important; }
</style>